.todo_panel_container {
  background-color: white;
  box-shadow: 0px 8px 11px -8px rgba(34, 60, 80, 0.09);
  margin-bottom: 15px;
  width: 100%;
  padding: 25px 20px;
  border-radius: 5px;

  .fields_container {
    display: flex;
    justify-content: space-between;
    gap: 10px;

    .field_container {
      width: 100%;
    }

    .field_container label {
      display: flex;
      flex-direction: column;
      gap: 5px;
      font-size: 15px;
    }

    .field_container input {
      font-size: 15px;
      padding: 10px 5px;
      border-radius: 2px;
      border: 0.5px solid black;
    }
  }

  .button_container {
    width: 100%;
    margin-top: 10px;
    text-align: right;
  }
}
