.todo_item_container {
  background-color: white;
  box-shadow: 0px 8px 11px -8px rgba(34, 60, 80, 0.09);
  margin-bottom: 15px;
  width: 100%;
  padding: 25px 20px;
  border-radius: 5px;

  .todo_item_title {
    font-size: 25px;
    cursor: pointer;
    font-weight: 600;
    margin-bottom: 15px;
  }

  .todo_item_description {
    font-size: 15px;
    margin-bottom: 15px;
  }

  .todo_item_button_container {
    display: flex;
    justify-content: right;
    gap: 5px;

    .todo_item_button_delete {
      border-radius: 5px;
      border: 1px solid rgba(210, 25, 25, 0.5);
      color: #d21919;
      padding: 8px 20px;
      cursor: pointer;
      background-color: white;
    }

    .todo_item_button_delete:hover {
      background-color: rgba(210, 25, 25, 0.05);
    }

    .todo_item_button_edit {
      border-radius: 5px;
      border: 1px solid rgba(210, 102, 25, 0.5);
      color: #d27c19;
      padding: 8px 20px;
      cursor: pointer;
      background-color: white;
    }

    .todo_item_button_edit:hover {
      background-color: rgba(210, 84, 25, 0.05);
    }
  }
}
